function GooglePlacesError(status, result, errorMessage) {
  Error.call(this);

  var message = `Google Places API rejected with status=${status}`;
  if (typeof Error.captureStackTrace === 'function') {
    Error.captureStackTrace(this, GooglePlacesError);
  } else {
    this.stack = (new Error(message)).stack;
  }

  this.message = message;
  this.status = status;
  this.result = result;
  this.googleErrorMessage = errorMessage;
  this.name = this.constructor.name;
}

GooglePlacesError.prototype = Object.create(Error.prototype);
GooglePlacesError.prototype.constructor = GooglePlacesError;

export default GooglePlacesError;
