// Formats a Date object as '06/16/2015 5:45pm'
module.exports = function(t) {
  t = new Date(t);
  return pad2(t.getMonth() + 1) + '/' + pad2(t.getDate()) + '/' + t.getFullYear() + ' ' +
    pad2(div12(t.getHours())) + ':' + pad2(t.getMinutes()) + ampm(t.getHours());
};

function div12(num) {
  return (num < 13) ? num : num - 12;
}

function ampm(num) {
  return (num < 13) ? 'am' : 'pm';
}

function pad2(num) {
  return (num < 10) ? '0' + num : num.toString();
}
