/* global google */

var { cloneDeep, pick } = require('lodash');
var request = require('../lib/request');
var loadGoogleMaps = require('../lib/load_google_maps');
var GooglePlacesError = require('../lib/errors/google_places_error');
var toRichText = require('../../shared/to_rich_text');
var autosave = require('./autosave');
var imageActions = require('./images');
var videoActions = require('./videos');
var oembedActions = require('./oembeds');
var sentryActions = require('./sentry');
var hermanoActions = require('./hermano');

// #### Defaults ####

// center: coords for the center of the contiguous US
// sw: coords to bound the sw of the contiguous US
// ne: coords to bound the ne of the contiguous US
export const DEFAULT_BOUNDS = {
  center: { lat: 40, lng: -98.5 },
  sw:     { lat: 25.91, lng: -123.36 },
  ne:     { lat: 49, lng: -66.87 }
};

// #### Helpers ####

function getPoint(index) {
  return app.state.get(['doc', 'head', 'contents', 'draft', 'map_points', index]);
}

// Set the initial center coords for the map:
// A) Use the current community's locale, if one exists
// B) Otherwise, use the first map point's coords, if any points exist
// C) If neither A nor B can happen, use the defaults
export function setInitialBounds(mapPoints) {
  var locale = app.state.get(['community', 'locale']);
  var bounds = cloneDeep(DEFAULT_BOUNDS);

  if (mapPoints.length > 0) {
    var mapPoint = mapPoints[0];
    bounds.center = { lat: mapPoint.latitude, lng: mapPoint.longitude };
  } else if (locale) {
    bounds.center = pick(locale, ['lat', 'lng']);
  }

  setMapBounds(bounds);
}

export function setMapBounds(bounds) {
  app.state.set('mapBounds', bounds);
}

// #### Map point editing ####

export function removeMapArt(index) {
  var docId = app.state.get(['doc', 'id']);
  var point = getPoint(index);
  if (point.image) {
    autosave.localUpdate(docId, [{ p: ['draft', 'map_points', index, 'image'], od: point.image }]);
  }
  if (point.video) {
    autosave.localUpdate(docId, [{ p: ['draft', 'map_points', index, 'video'], od: point.video }]);
  }
  if (point.oembed) {
    autosave.localUpdate(docId, [{ p: ['draft', 'map_points', index, 'oembed'], od: point.oembed }]);
  }
}

export function editMapArt(index) {
  var docId = app.state.get(['doc', 'id']);
  var point = getPoint(index);
  if (point.image) {
    imageActions.getAndEditImage(point.image, 'image_mapart', { docId, path: ['draft', 'map_points', index, 'image'] });
  } else if (point.video) {
    videoActions.getAndEditVideo(point.video, 'Update', { docId, path: ['draft', 'map_points', index, 'video'] });
  } else if (point.oembed) {
    oembedActions.editOembed(point.oembed, 'Update', { docId, path: ['draft', 'map_points', index, 'oembed'] });
  }
}

export function moveMapPoint(oldIndex, newIndex) {
  autosave.localUpdate(app.state.get(['doc', 'id']), [{ p: ['draft', 'map_points', oldIndex], lm: newIndex }]);
}

export function removeMapPoint(index) {
  autosave.localUpdate(app.state.get(['doc', 'id']), [{ p: ['draft', 'map_points', index], ld: getPoint(index) }]);
}

// #### Google Places API autocomplete ####

// Runs an autocomplete query against the Google Places ID and returns a formatted
// result for our autocompleter
export function autocomplete(searchQuery) {
  return loadGoogleMaps()
    .then(() => buildGooglePlacesQuery(searchQuery))
    .then(query => runGooglePlacesQuery(query))
    .catch(sentryActions.handleError);
}

function buildGooglePlacesQuery(searchQuery) {
  var query = { input: searchQuery };

  // If there are bounds in state and they aren't the default coords, use
  // them for location biasing
  var { center, sw, ne } = app.state.get('mapBounds');
  if (center && !pointsEqual(center, DEFAULT_BOUNDS.center)) {
    query.location = `${center.lat},${center.lng}`;

    // Get the radius based on the distance between the current map bounds
    var swLatLng = new google.maps.LatLng(sw.lat, sw.lng);
    var neLatLng = new google.maps.LatLng(ne.lat, ne.lng);
    query.radius = google.maps.geometry.spherical.computeDistanceBetween(swLatLng, neLatLng) / 2;
  }

  return query;
}

function runGooglePlacesQuery(query) {
  return request('/api/google/places/autocomplete/json', { query })
    .then(({ status, predictions, error_message: errorMessage }) => {
      if (status === 'OK' || status === 'ZERO_RESULTS') {
        return predictions.map(({ structured_formatting: address, place_id: placeId }) => {
          return {
            text: address.main_text,
            additional_text: address.secondary_text,
            value: {
              google_place_id: placeId
            }
          };
        });
      }

      return Promise.reject(new GooglePlacesError(status, predictions, errorMessage));
    });
}

// Gets details for the given id from the Google Places ID and returns a
// formatted map point to be saved
function buildPointFromGooglePlaceId(placeId) {
  if (!placeId) {
    return Promise.reject(new Error('google_place_id is required'));
  }
  return request('/api/google/places/details/json', {
    query: { placeid: placeId }
  }).then(({ status, result, error_message: errorMessage }) => {
    if (status === 'OK') {
      // common map point data needed to create a map point in state, as well as to send to hermano
      var mapPointData = {
        lat:      result.geometry.location.lat,
        lng:      result.geometry.location.lng,
        phone:    result.formatted_phone_number || '',
        url:      result.website || '',
        google_place_id: placeId
      };

      // map point to save into state
      var point = buildMapPoint(Object.assign({}, mapPointData, { title: result.name, address: result.formatted_address }));

      // hermano only supports price tiers on a scale of 1-4, but google place results are on a scale of 0-4
      // to make sure we don't run into hermano validation errors, any google place result with a 0 price tier
      // will be bumped to a 1
      var priceTier = result.price_level === 0 ? 1 : result.price_level;

      // parse address components, which may not all exist for a given place result
      var addressComponents = result.address_components;
      var streetNumber = addressComponents.find(c => c.types.includes('street_number'));
      var route = addressComponents.find(c => c.types.includes('route'));
      var naturalFeature = addressComponents.find(c => c.types.includes('natural_feature'));
      var address;
      if (streetNumber && route) {
        address = `${streetNumber.long_name} ${route.long_name}`;
      } else if (route) {
        address = route.long_name;
      } else if (naturalFeature) {
        address = naturalFeature;
      }
      var city = addressComponents.find(c => c.types.includes('sublocality'));
      var state = addressComponents.find(c => c.types.includes('administrative_area_level_1'));
      var zip = addressComponents.find(c => c.types.includes('postal_code'));

      mapPointData = Object.assign({}, mapPointData, {
        name: result.name,
        google_place_id: placeId,
        price_tier: priceTier,
        address: address || '',
        city: city ? city.long_name : '',
        state: state ? state.short_name : '',
        zip: zip ? zip.long_name : ''
      });

      return hermanoActions.hermanoIdForMapPoint(mapPointData).then((hermanoId) => {
        point.hermano_id = hermanoId;
        return point;
      }).catch((err) => {
        sentryActions.reportError(err, false);
        return point;
      });
    }

    return Promise.reject(new GooglePlacesError(status, result, errorMessage));
  }).catch(sentryActions.handleError);
}

// #### Shared API logic ####

// Based on the data passed in, create a map point from API details and save
// to the next position in state
export function createMapPoint(data, index) {
  return buildPointFromGooglePlaceId(data.google_place_id).then(point => {
    autosave.localUpdate(app.state.get(['doc', 'id']), [{
      p: ['draft', 'map_points', index],
      li: point
    }]);
  }).catch(sentryActions.handleError);
}

function buildMapPoint({ lat, lng, title, address, phone, url, google_place_id: googlePlaceId }) {
  return {
    latitude:   lat,
    longitude:  lng,
    title:      toRichText(title),
    address:    toRichText(address),
    phone:      toRichText(processPhoneNumber(phone)),
    website:    toRichText(url),
    body:       toRichText(''),
    google_place_id: googlePlaceId
  };
}

// Takes an unformatted phone number (i.e. just 10 digits) and formats it like:
// 1234567890 => (123) 456-7890
function processPhoneNumber(number) {
  if (!/\d{10}/.test(number)) {
    return number;
  }
  return `(${number.substring(0, 3)}) ${number.substring(3, 6)}-${number.substring(6)}`;
}

// HACK: use parseFloat and toFixed to determine if two points have
// a lat and lng within 4 decimal points of each other
function pointsEqual(point1, point2) {
  return (
    point1.lat.toFixed(4) === point2.lat.toFixed(4) &&
    point1.lng.toFixed(4) === point2.lng.toFixed(4)
  );
}
