const GOOGLE_MAPS_URL = `https://maps.googleapis.com/maps/api/js?key=${app.config.googleApiKey}&libraries=geometry&callback=googleMapsCallback`;

export default function loadGoogleMaps() {
  // If the Google Maps SDK is already mounted to the client context, resolve now
  if (window.google && window.google.maps) {
    return Promise.resolve();
  }

  // Promisify the SDK load, simply resolve when finished
  return new Promise(resolve => {
    window.googleMapsCallback = resolve;

    var script = document.createElement('script');
    script.type = 'text/javascript';
    script.src = GOOGLE_MAPS_URL;
    document.body.appendChild(script);
  });
}
